import { useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import Re from './images/R.jpg';
import A from './images/a.jpg';
import L from './images/e.jpg';


function Kinde() {
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex) => {
    setIndex(selectedIndex);
  };

  return (
    
    <Carousel activeIndex={index} onSelect={handleSelect} style={{backgroundColor:'#f8f8f8'}}>
      <Carousel.Item id='two'>
        <img src={L} text="First slide" alt='hi'/>
        {/* <Carousel.Caption>
          <h3>First slide label</h3>
          <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
        </Carousel.Caption> */}
      </Carousel.Item>
      <Carousel.Item id='two'>
        <img src={A} text="Second slide" alt='lu' />
        {/* <Carousel.Caption>
          <h3>Second slide label</h3>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
        </Carousel.Caption> */}
      </Carousel.Item>
      <Carousel.Item id='two'>
        <img src={Re} text="Third slide" alt='ji' />
        {/* <Carousel.Caption>
          <h3>Third slide label</h3>
          <p>
            Praesent commodo cursus magna, vel scelerisque nisl consectetur.
          </p>
        </Carousel.Caption> */}
      </Carousel.Item>
    </Carousel>
   
  );
}

 export default Kinde;  