import React, { useState, useEffect } from "react";

import image1 from "./images/e.jpg";
import image2 from "./images/a.jpg";
import image3 from "./images/R.jpg";

const HeroSlider = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const images = [image1, image2, image3];
  const heroTexts = ["Nasze szkolenia BHP to klucz do minimalizacji ryzyka wypadków i zapewnienia przestrzegania przepisów.", "Bezpieczeństwo pracowników to nasza misja! Wspólnie dbamy o ich pewność w miejscu pracy.", "Zapewniamy najwyższej jakości urządzenia gaśnicze"];
  const heroSubTexts = ["", "", ""];
const bgColors = ['rgba(0, 0, 0, 0.5)', '#ff6b6b', '#6b5f87', '#ff6b81'];


  useEffect(() => {
    const interval = setInterval(goToNextSlide, 5000);
    return () => clearInterval(interval);
  }, [currentIndex]);

  const goToPrevSlide = () => {
    const lastIndex = images.length - 1;
    const shouldResetIndex = currentIndex === 0;
    const index = shouldResetIndex ? lastIndex : currentIndex - 1;
    setCurrentIndex(index);
  };

  const goToNextSlide = () => {
    const lastIndex = images.length - 1;
    const shouldResetIndex = currentIndex === lastIndex;
    const index = shouldResetIndex ? 0 : currentIndex + 1;
    setCurrentIndex(index);
  };

  const isActiveSlide = (index) => {
    return currentIndex === index;
  };

  return (
    <div className="hero-slider">
      {images.map((image, index) => {
        return (
          <div
            key={index}
            className={`hero-image ${isActiveSlide(index) ? 'active' : ''}`}
            style={{
              backgroundImage: `url('${image}')`,
              backgroundSize: "cover",
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundColor: bgColors[index],
            }}
            onClick={goToNextSlide}
          >
            <div className="hero-container">
              <h1 className="hero-text">{heroTexts[index]}</h1>
              <p className="hero-subtext">{heroSubTexts[index]}</p>
            </div>
          </div>
        );
      })}
      <button className="slider-prev" onClick={goToPrevSlide}>
        {"<"}
      </button>
      <button className="slider-next" onClick={goToNextSlide}>
        {">"}
      </button>
    </div>
  );
};

export default HeroSlider;